function ButtonNewQuote(props) {
  return (
    <button 
      id="new-quote"
      className="ButtonNewQuote"
      type="button"
      onClick={props.handleClickBtn}
    > 
      NEW QUOTE
    </button>
  )
}

export default ButtonNewQuote

{/* <p>When the #new-quote button is clicked, my quote machine should fetch a new quote and display it in the #text element.</p>
    <p>My quote machine should fetch the new quote's author when the #new-quote button is clicked and display it in the #author element.</p> */}
