function LinkTweetQuote(props) {
  return (
    <div className="LinkTweetQuote">
      <a 
        id="tweet-quote"
        className="tweet-quote" 
        href={`https://twitter.com/intent/tweet?text=${props.quoteText}
              
                                                ~ ${props.quoteAuthor}`} 
        target="_blank"
      >
        <svg 
          width="100" 
          height="100"  
          viewBox="0 0 32 32"
        >
          <title>Tweet this quote!</title>
          <path 
            d="M2 4 C6 8 10 12 15 11 A6 6 0 0 1 22 4 A6 6 0 0 1 26 6 A8 8 0 0 0 31 4 A8 8 0 0 1 28 8 A8 8 0 0 0 32 7 A8 8 0 0 1 28 11 A18 18 0 0 1 10 30 A18 18 0 0 1 0 27 A12 12 0 0 0 8 24 A8 8 0 0 1 3 20 A8 8 0 0 0 6 19.5 A8 8 0 0 1 0 12 A8 8 0 0 0 3 13 A8 8 0 0 1 2 4">
          </path>
        </svg>
      </a>  
    </div>
  )
}

export default LinkTweetQuote


{/* <p>I can tweet the current quote by clicking on the #tweet-quote a element. This a element should include the "twitter.com/intent/tweet" path in its href attribute to tweet the current quote.</p> */}
    