import { useCallback, useEffect, useState } from "react"
import Author from "./Author"
import ButtonNewQuote from "./ButtonNewQuote"
import LinkTweetQuote from "./LinkTweetQuote"
import Text from "./Text"

function QuoteBox() {
  // state responseData utk store response data from API call
  const [responseData, setResponseData] = useState([{}])
  // const [textColor, setTextColor] = useState('')
  // const [backgroundColor, setBackgroundColor] = useState('')
  // API call disimpan dalam variable/function fetchData agar tidak hanya dipanggil/happens di dlm useEffect() tapi,
  // juga bisa dipanggil saat button new quote diklick
  // gunakan useCallback() hook, To stop the infinite loops of the side effects of the API call 
  // This hook returns a memoized callback that only changes if the dependencies of the function change
  const fetchData = useCallback(() => {
    fetch("https://type.fit/api/quotes")
      .then((response) => {
        return response.json()
      })
      .then((data) => {
        // response data saved to the state responseData
        setResponseData(data)
        console.log(data)
      })
      .catch((error) => {
        console.log(error)
      })
  }, [])

  useEffect(() => {
    // API call happens when the component is mounted/dipasang
    fetchData()
  }, [fetchData])

  // get random quote of state responseData and saved to variable quote 
  let quote = responseData[Math.floor(Math.random() * responseData.length)]

  return(
    <div id="quote-box">
      <LinkTweetQuote quoteText={quote.text} quoteAuthor={quote.author === null ? "Anonymous" : quote.author} />
      <div className="Quote-box">
        <Text quoteText={quote.text} />
        <Author quoteAuthor={quote.author === null ? "Anonymous" : quote.author} />  
        <ButtonNewQuote handleClickBtn={fetchData}/>
      </div>
    </div>
  ) 
}

export default QuoteBox

// The application will fetch a quote to display when the component mounts because of the function call in useEffect. 
// Then, if the user wants a new quote they can click the button later on!